img{
    width: 100%;
    height: auto;
}

#header{
    width: 100%;
    overflow: hidden;
}
#header a svg{
    transition: color 300ms;
}
#header a:hover svg{
    color: white !important;
}
#header .backer{
    position: absolute;
    top: 0px;
    right: 0px;
    z-index: 999;
    background: hsla(2, 50%, 54%, 0.7);
    padding: 40px 40px 220px 35px;
    border-bottom-left-radius: 150px;
    border-top-left-radius: 10px;
    border-bottom-right-radius: 10px;
    cursor: pointer;
    transition: all 500ms;
}
#header .backer:hover{
    background-color:hsla(49, 71%, 59%, 0.8);
    -webkit-transform: rotateZ(-10deg);
    -ms-transform: rotateZ(-10deg);
    transform: rotateZ(-15deg) translateX(-50px);
    border-bottom-left-radius: 80px;
    border-top-right-radius: 100px;
}
#header .backer div{
    transition: all 300ms;
}
#header .backer:hover div{
    transform: rotate(40deg);
}
#header .backer a{
    color: #2b2b2b !important;
    transition: all 500ms;
    font-weight: 600;
    font-family: cursive, sans-serif;
}
#header .backer a:hover{
    text-decoration: none;
    color:  rgb(217, 92, 91) !important;
}

#about {
    width: 100% !important;
    overflow: hidden;
    background-color: #2B2B2B;
    padding: 50px 25px;
}

#resume{
    width: 100% !important;
    overflow: hidden;
    background-color: #ededed;
    padding: 50px 25px;
}
#resume .education{
    padding-left: 10px;
}

#resume a{
    color: rgb(217, 92, 91);
}

#footer .container-fluid{
    padding: 50px 20px;
    background-color: rgba(187, 72, 71, 0.9);
}
#footer a {
    color: #ededed;
    text-shadow: black 0 1px 1px;
}

#footer .footer-end{
    padding: 10px;
}

#footer .go-up svg{
    border: solid #373737 1px;
    padding: 0 5px;
    font-size: 50px;
    color: #111;
    transition: all 500ms;
    cursor: pointer;
}
#footer .go-up svg:hover{
    border-color: transparent;
}

@media only screen and (max-width: 778px){
    #footer .go-up{
        display: none;
    }
}

@keyframes animateBorderColor{
    0%{
        border-color: hsl(54, 77%, 57%);
    }
    25%{
        border-color: hsl(79, 74%, 55%);
    }
    50%{
        border-color: hsl(156, 78%, 55%);
    }
    75%{
        border-color: hsl(183, 78%, 55%);
    }
    100%{
        border-color: hsl(212, 73%, 60%);
    }
}
.animate-border-color{
    animation: animateBorderColor 4000ms alternate-reverse infinite;
    margin-top: 11px;
    filter: contrast(95%) brightness(85%);
}