.avatar{
    margin-top: 40px;
    margin-bottom: 20px;
    width: 220px;
    height: 220px;
    border-radius: 100%;
    overflow: hidden;
    box-shadow: #3a3a3a -1px 3px 5px;
}
.avatar img{
    width: 100%;
    height: auto;
}

@media only screen and (max-width: 768px) {
    .avatar{
        margin-top: 30px;
        width: 200px;
        height: 200px;
    }
}
@media only screen and (max-width: 576px) {
    .avatar{
        margin-top: 20px;
        margin-bottom: 20px;
        width: 100px;
        height: 100px;
    }
    h1{
        font-size: xx-large;
    }
}